$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.settingMenu {
    border: $border;
    border-radius: $radius;
    &-item {
        position:relative;
        padding:  8px 16px;
        cursor: pointer;
        &:not(:last-child) {
            border-bottom: 1px solid $border-color;
        }
        &:hover {
            background-color: rgba(234, 238, 242, 0.5);
        }
    }

    &-item_active {
        background-color: rgba(234, 238, 242, 0.5);
        &::before {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            bottom: 0;
            width: 2px;
            background-color: rgb(253, 140, 115);
        }
    }
}