$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.settings {
    @include blank_page;
    display: flex;

    &-sider {
        width: 300px;
        margin-right: 24px;
    }
    &-content {
        flex: 1;
        &-header {
            padding-bottom: 8px;
            margin-bottom: 16px;
            border-bottom: $border;
        }
    }
}